import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';
import { Subscription } from 'rxjs/Subscription';

import textField from 'muse-ui/textField';

import Utils from '~/utils';
import { CACHE_NAME, IVerificationConfig } from '~/cache';
import { Subject } from 'rxjs/Subject';
import { ToastMsg } from '~/entity/toast';
import Api from '~/api';

@Component({
    components: {
        'mu-text-field': textField
    }
})
export default class VerificationCodeComp extends Vue {

    @Action('showToast')
    showToast;

    phone: string = null;

    code: string = null;

    buttonTxt: string = null;

    subscription: Subscription;

    subject: Subject<any>;  

    buttonEnable = true;

    // submitEnable = false;
    hasCode = false;

    type: string = null;

    get submitEnable() {
        if (!this.hasCode) {
            return false;
        } else if (this.code.length >= 6) {
            return true;
        }
        return false;
    }

    activated() {
        // this.phone = '13925210768';
        const config: IVerificationConfig = Utils.getItem(CACHE_NAME.verificationConfig);
        this.phone = config.phone;
        this.subject = config.subject;
        this.subscription = config.subscription;
        this.buttonEnable = true;
        this.type = config.type;
        this.hasCode = false;
        this.code = '';

        // 自动获取验证码
        this.getCode();
    }
    
    deactivated() {
        this.subscription.unsubscribe();
    }

    async getCode() {
        this.buttonEnable = false;
        this.buttonTxt = '正在获取';

        try {
            const res = await Api.getVerificationCode(this.type);

            this.buttonTxt = '获取成功';
            this.hasCode = true;

            let second = 60;

            const timer = setInterval(() => {
                if (second === 0) {
                    clearInterval(timer);
                    this.buttonTxt = '重新获取';
                    this.buttonEnable = true;
                    return;
                }
                this.buttonTxt = second + 's';
                second--;
            }, 1000);

        } catch (e) {
            this.showToast(new ToastMsg(e.message));
            this.buttonTxt = '重新获取';
            this.buttonEnable = true;
        }
    }
  
    submit() {
        const code = this.code;
        // 提交验证码
        this.subject.next(code);
    }
}



